.root {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
  border-left: 2px solid transparent;
  height: 48px;
  padding-left: 24px;
  padding-right: 24px;
  color: #323232;
  list-style-type: none;
  text-align: left;
}

@media (max-width: 600px) {
  .root {
    padding-left: 12px;
    padding-right: 12px;
  }
}

.root-active {
  background-color: rgb(239, 246, 252);
  border-left-color: rgb(37, 100, 207);
}

.button {
  appearance: none;
  border: none;
  background: transparent;
  width: 100%;
  height: 100%;
  font-size: inherit;
  text-align: left;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
