.root {
  box-sizing: border-box;
  width: 100%;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  margin-bottom: 24px;
}

.input {
  box-sizing: border-box;
  display: block;
  font-size: inherit;
  width: 100%;
  height: 48px;
  border: none;
  color: #323232;
  padding: 16px;
  -webkit-tap-highlight-color: transparent;
}
