.app {
  text-align: center;
  height: 100%;
}

.app-body {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: calc(100% - 40px);
  background: #fff;
}

.app-body-menu-closed {
  transform: translateX(-276px);
  visibility: hidden;
}

.app-body-menu-opening {
  transition: transform 0.3s;
  transform: translateX(0);
}

.app-body-menu-opened {
  transform: translateX(0);
}

.app-body-menu-closing {
  transition: transform 0.3s;
  transform: translateX(-276px);
}

.app-task-list-container {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding-bottom: 24px;
  width: 100%;
  height: 100%;
  max-width: 276px;
  margin: 0 auto;
  background: #fff;
  overflow: auto;
  z-index: 1;
}

.app-task-container {
  box-sizing: border-box;
  margin-top: 48px;
  padding: 24px;
  flex: 1;
}

@media (max-width: 600px) {
  .app-task-container {
    padding: 24px 12px;
  }
}
