.app {
  text-align: center;
  height: 100%;
}

.app-body {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: calc(100% - 48px);
  background: #fff;
}

.app-body-menu-closing {
  width: calc(100% + 290px);
  transition: transform 0.3s;
  transform: translateX(-290px);
}

.app-body-menu-opening {
  width: calc(100%);
  transition: transform 0.3s;
  transform: translateX(0);
}

.app-body-menu-closed {
  width: calc(100% + 290px);
  transform: translateX(-290px);
}

.app-body-menu-opened {
  transform: translateX(0);
}

.app-task-list-container {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding-top: 24px;
  padding-bottom: 24px;
  width: 100%;
  height: 100%;
  max-width: 290px;
  margin: 0 auto;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0.3px 0.9px 0px,
    rgba(0, 0, 0, 0.1) 0px 1.6px 3.6px 0px;
  overflow: auto;
}

.app-task-list-container-placeholder {
  width: 290px;
}

.app-task-container {
  box-sizing: border-box;
  padding: 24px;
  flex: 1;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

