.app-task-list-container-header {
  height: 48px;
  background: cornflowerblue;
  margin-bottom: 24px;
  padding: 0 24px;
  display: flex;
  align-items: center;
  font-size: 20px;
  color: #fff;
}

@media (max-width: 600px) {
  .app-task-list-container-header {
    padding: 0 12px;
  }
}

.button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  appearance: none;
  background: transparent;
  border: none;
  width: 44px;
  height: 44px;
  padding: 0;
  margin: 0;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.button:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px #fff;
}

.icon-container {
  position: relative;
  width: 26px;
  height: 26px;
}

.bar {
  background: #fff;
  height: 3px;
  width: 26px;
  position: absolute;
  top: 50%;
  left: 0;
}

.icon-container .bar:first-child {
  transform: translateY(-50%) rotate(45deg);
}

.icon-container .bar:last-child {
  transform: translateY(-50%) rotate(-45deg);
}
