.app-task-list-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s;
  opacity: 0;
}

.app-task-list-overlay-closed {
  opacity: 0;
  width: 0px;
  height: 0px;
}

.app-task-list-overlay-opening {
  opacity: 1;
}

.app-task-list-overlay-closing {
  transition: opacity 0.3s;
  opacity: 0;
}

.app-task-list-overlay-opened {
  opacity: 1;
}
